#note {
    height: 250px;
    flex-basis: calc(33% - 2rem);
    margin: 0.5rem;
    padding: 0.5rem;
    border: 1px solid ;
    // border-radius: 0.5rem;
    background-color: beige;
    
    span {
      font-size: 1rem;
      margin-bottom: 0.5rem;
      }
  
    #note-menu {
      display: flex;
      justify-content: space-between;
  
      > span:nth-last-child(1) {
        > span {
          margin-right: 0.25rem;
          margin-left: 0.25rem;
          transition: 0.25s;
          cursor: pointer;
        }
  
        > span:nth-child(1) {
          &:hover {
            color: green;
          }
        }
  
        > span:nth-child(2) {
          &:hover {
            color: red;
          }
        }
      }
    }
    
     #date {
      font-size: 1rem;
      margin-bottom: 0.5rem;
    }
  }